<template>
	<div class="showtu-container">
		<div class="showtu-header">
			<div class="showtu-zhong">
				<div class="showtu-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="showtu-ti">{{panels_title}}</div>
				<div></div>
			</div>
		</div>
		<div class="showtu-content">
			<div class="showtu-wap">
				<div class="showtu-around" v-for="(item,index) in atlass_list" :key="index">
					<div class="showtu-tupian" @click="openSimpleDialog(item,index)">
						<!--<img src="../assets/img_tl3.png">-->
						<img :src="!!item.atlas_pic?baseUrl+item.atlas_pic:'../assets/img_tl3.png'">
					</div>
				</div>
			</div>
			<mu-container>
			  <mu-dialog width="100%" :open.sync="openSimple">
			  	<div class="showtu-lun">
			  		<mu-carousel interval='80000' @change="change()" :active='active'>
						<mu-carousel-item v-for="(item,index) in atlass_list" :key="index">
							<img :src="baseUrl+item.atlas_pic" @click="openBotttomSheet(item)">
						</mu-carousel-item>
					</mu-carousel>
			  	</div>
			  	<!--<div class="showtu-tantu" @click="openBotttomSheet">
			  		<img :src="!!img_url?baseUrl+img_url:'../assets/img_tl3.png'">
			  	</div>-->
			  </mu-dialog>
			</mu-container>
			<mu-bottom-sheet :open.sync="open">
			    <mu-list @item-click="closeBottomSheet">
			      <mu-sub-header>
			      	<div class="showtu-between">
			      		<div class="showtu-flex" @click="save()">
				      		<div class="showtu-xiazai">
				      			<img src="../assets/xiazai.png">
				      		</div>
				      		<div class="showtu-baocun">保存</div>
				      	</div>
				      	<div class="showtu-qux" @click=" closeSimpleDialog()">取消</div>
			      	</div>
			      </mu-sub-header>
			    </mu-list>
			</mu-bottom-sheet>
		</div>
	</div>
</template>
<script>
	import Qs from "qs"
	export default {
		name: 'home',
		data() {
			return {
				active:'',
				img_url: '',
				msg: '',
				data: [],
				atlass_list: '',
				panelsid: '',
				openSimple: false,
				open: false,
				this_opacity: '1'
			}
		},
		methods: {

			openSimpleDialog (item,index) {
				this.active = index
				let that = this
				that.img_url = item.atlas_pic;
				that.openSimple = true;
//				setTimeout(function(){ 
//					that.openSimple = true;
//				}, 1000);
				
		    },
		    closeSimpleDialog () {
		      this.openSimple = false;
		      this.closeBottomSheet()
		    },
			closeBottomSheet () {
		      this.open = false;
		    },
		    openBotttomSheet (item) {
		    	this.img_url = item.atlas_pic;
		      this.open = true;
		    },
			back() {
				window.history.back();
			},
			atlass() {
				var that = this;
				var params = {
					panelsid: that.panels_id
				};
				that.$http.post('/panels/atlass', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.atlass_list = res.data;
					}
				})
			},
//			保存图片
			save(img_url){
				var that = this
				dsBridge.call('saveImg',that.baseUrl+that.img_url);
			}
		},
		created() {
			// 页面刚加载的时候执行
//			this.panels_id = this.$route.params.id;
			this.panels_id = this.$route.query.type;
			this.panels_title = this.$route.query.typeone;
			this.atlass();

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.showtu-container {
		width: 100%;
		height: 100%;
		background: #FFFFFF;
	}
	
	.showtu-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		margin-bottom: 1.5rem;
		border-bottom: 1px solid #eaeaea;
	}
	
	.showtu-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.showtu-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.showtu-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
	}
	.showtu-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
		/*padding-left: 1.6rem;
		padding-right: 1.5rem;*/
	}
	.showtu-wap{
		display: flex;
		flex-wrap: wrap;
	}
	.showtu-around  {
		display: flex;
		justify-content: space-around;
		width: 33.33%;
		/*padding-top: 1rem;*/
		padding-bottom: 1rem;
	}
	.showtu-tupian img{
		width: 100%;
		height: auto;
		padding: 0 0.5rem;
	}
	.showtu-tantu{
		margin-bottom: -1rem;
	}
	.showtu-tantu img{
		width: 100%;
		height: auto;
		margin: 0 auto;
	}
	.showtu-between{
		display: flex;
		justify-content: space-between;
	}
	.showtu-flex{
		display: flex;
	}
	.showtu-xiazai img{
		height: 2rem;
		width: auto;
		margin-top: 1rem;
		margin-right: 1rem;
	}
	.showtu-baocun{
		font-size: 1.5rem;
		color: #000;
		/*text-align: center;*/
	}
	.showtu-qux{
		font-size: 1.5rem;
		padding-right: 1.5rem;
		color: #000000;
	}
	/deep/ .mu-dialog-body{
		padding: 0;
	}
	/deep/ .mu-dialog{
		max-width : 100%;
		background: #000000;
		-webkit-box-shadow: none;
		box-shadow: none;
	}
	/*轮播图*/
	.showtu-lun{
		width: 100%;
		height: 100%;
		position: relative;
	}
	.showtu-lun img {
		width: 100%;
		/*height: 100%;*/
		height: auto;
		margin: 0 auto;
	}

	/deep/ .mu-tab {
		min-width: 0;
	}
	
	/*/deep/ .mu-carousel-indicator-icon {
		background: #fe4563;
	}*/
	
	/deep/ .mu-carousel-button.mu-icon-button {
		font-size: 0;
		z-index: 0;
	}
	/deep/ .mu-carousel {
		width: 100%;
		height: 75rem;
	}
</style>


